<template>
  <div> 
    <router-view></router-view>
    <div class="tabar">
      <van-tabbar v-model="active">
        <van-tabbar-item v-for="(item,index) in tabar" :key="index" :icon="item.icon" @click="onChange(item)">{{item.name}}</van-tabbar-item>
      </van-tabbar>
    </div>
  </div>
</template>

<script>
import "../assets/css/home.css";
export default {
  data() {
    return {
      active: 0,
      getMessagee:'',
      image_src: '',
      tabar: [
        {name: '首页',icon: 'home-o',routsName:'Sort',routename:["home/sort"]},
        {name: '分类',icon: 'orders-o',routsName:'SuperMarket',routename:["home/supermarket"]},
        {name: '购物车',icon: 'cart-o',routsName:'Seckill',routename:["home/seckill"]},
        {name: '我的',icon: 'user-o',routsName:'Products',routename:["home/products"]}
      ]
    }
  },
  created() {
    this.getData();
    let url = location.hash
      .slice(2)
      .split("/")
      .splice(0, 2)
      .join("/");
    for (let i = 0; i < this.tabar.length; i++) {
      if (this.tabar[i].routename.indexOf(url) > -1) {
        this.active = i;
        break;
      }
    }
  },
  methods: {
    onChange(item){
      this.$router.push({name:item.routsName})
    },

    getData(){
      this.axios({
        method: 'GET',
        url: '/home/catitems',
        data: {}
      }).then(result => {
        if(result.status == 200){
          this.getMessagee = result.data.message;
        }
      }).catch(err => {
        console.log('err ==>', err)
      })
    }
  },
 
};
</script>